<template>
  <div class="mt-3 md:mt-10 md:p-2">
    <p class="md:font-medium md:text-lg">
      Stake {{ pool.lpName }} token to earn SPOON rewards
    </p>
    <p
      v-if="pool.subTitle"
      v-html="pool.subTitle"
      class="mt-1 text-sm text-gray-700"
    ></p>
    <div @click="isShowAction = !isShowAction" class="border-2 p-2 md:p-5 mt-2">
      <div class="grid grid-cols-10 text-xs md:font-bold md:text-lg">
        <div class="col-span-2">
          <img class="w-6 md:w-12 inline z-40" :src="pool.token0src" />
          <img
            class="w-6 md:w-12 inline z-0 -ml-3 md:-ml-4"
            :src="pool.token1src"
          />
        </div>
        <div class="col-span-2 md:col-span-3 m-auto">{{ pool.lpName }}</div>
        <div class="col-span-3 md:col-span-2 m-auto">
          <span class="rounded md:rounded-xl bg-green-400 text-white p-1 md:p-2"
            >APY {{ apy }}%</span
          >
        </div>
        <div class="col-span-2 m-auto">
          SPOON Earned: {{ formatBN(rewards) }}
        </div>
        <div class="col-span-1 m-auto font-normal text-gray-500">
          <i v-show="isShowAction" class="icon ion-chevron-down"></i>
          <i v-show="!isShowAction" class="icon ion-chevron-right"></i>
        </div>
      </div>
    </div>
    <div v-if="isShowAction" class="border-2 border-t-0 p-1 md:p-5 bg-gray-100">
      <div class="flex-row md:grid md:grid-cols-11 text-gray-400">
        <div class="col-span-4 p-3">
          <p class="font-bold">I want to stake</p>
          <p>Available {{ pool.lpName }} token Balance: {{ balance }}</p>
          <div class="pt-2 pb-2">
            <input
              :disabled="needApprove || balance == '0.0'"
              v-model="stakeAmount"
              class="text-xl pl-1 w-9/12 text-green-400 border-2 border-r-0"
              type="number"
            />
            <button
              @click="stakeAmount = balance"
              class="text-xl text-green-400 border-2 w-3/12"
            >
              Max
            </button>
          </div>
          <Button
            v-if="needApprove"
            @click="onApprove"
            class="w-full"
            :loading="approving"
          >
            Approve
          </Button>
          <Button
            v-else
            :disabled="balance == '0.0'"
            @click="onStake"
            class="w-full"
            :loading="staking"
          >
            Stake
          </Button>
        </div>
        <div class="col-span-4 p-3">
          <p class="font-bold">I want to unstake</p>
          <p>Staked {{ pool.lpName }} token Balance: {{ staked }}</p>
          <div class="pt-2 pb-2">
            <input
              v-model="withdrawAmount"
              :disabled="staked == '0.0'"
              class="text-xl pl-1 w-9/12 text-green-400 border-2 border-r-0"
              type="number"
            />
            <button
              @click="withdrawAmount = staked"
              class="text-xl text-green-400 border-2 w-3/12"
            >
              Max
            </button>
          </div>
          <Button
            @click="onUnstake"
            :disabled="staked == '0.0'"
            class="w-full"
            :loading="unstaking"
          >
            Unstake
          </Button>
        </div>
        <div class="col-span-3 md:pl-4 md:border-l-2 text-center">
          <p>Total SPOON rewards:</p>
          <p class="text-3xl text-green-400 pt-6 pb-6">{{ rewards }}</p>
          <Button @click="onClaim" class="w-full" :loading="claiming">
            Claim
          </Button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Button from "./Button";
import { getFairLaunchContract, getProvider } from "@/utils/initWeb3";
import { ethers } from "ethers";
import PancakeSwapABI from "@/assets/abi/PancakeSwap.json";
import ERC20 from "@/assets/abi/ERC20.json";
import {
  fairLaunchAddress,
  busdAddress,
  pancakeSwapRouter,
  alpacaAddress,
  wbtcAddress,
  akitaAddress,
} from "@/config";
export default {
  components: {
    Button,
  },
  props: {
    pool: null,
    totalAllocPoint: null,
    rewardsPerBlock: null,
  },
  data() {
    return {
      tokenPrice: null,
      needApprove: false,
      approving: false,
      isShowAction: false,
      staking: false,
      unstaking: false,
      claiming: false,
      apy: "...",
      balance: "0",
      staked: "0",
      rewards: "0",
      stakeAmount: "0",
      withdrawAmount: "0",
      stakeToken: null,
      poolInfo: null,
    };
  },
  watch: {
    "$store.state.account": async function(account) {
      if (account) {
        await this.getTokenPrice();
        this.loadData();
      }
    },
  },
  async mounted() {
    if (this.$store.state.account) {
      await this.getTokenPrice();
      this.loadData();
    }
  },
  methods: {
    async getTokenPrice() {
      try {
        const pancakeSwap = new ethers.Contract(
          pancakeSwapRouter,
          PancakeSwapABI,
          getProvider()
        );
        this.tokenPrice = await pancakeSwap.getAmountsOut(
          ethers.utils.parseEther("1"),
          [akitaAddress, busdAddress]
        );
        this.tokenPrice = this.tokenPrice[this.tokenPrice.length - 1];
      } catch (error) {
        console.log("getTokenPrice", error);
      }
    },
    async onApprove() {
      this.approving = true;
      try {
        const tx = await this.stakeToken.approve(
          fairLaunchAddress,
          ethers.BigNumber.from("2")
            .pow("256")
            .sub("1")
        );
        await tx.wait();
        this.needApprove = false;
      } catch (error) {
        console.log("onApprove", error);
      } finally {
        this.approving = false;
      }
    },
    async onStake() {
      this.staking = true;
      try {
        const tx = await getFairLaunchContract().deposit(
          this.$store.state.account,
          this.pool.id,
          ethers.utils.parseUnits(this.stakeAmount, this.pool.decimals)
        );
        await tx.wait();
        this.loadData();
      } catch (error) {
        console.log("onStake", error);
      } finally {
        this.staking = false;
      }
    },
    async onUnstake() {
      this.unstaking = true;
      try {
        const tx = await getFairLaunchContract().withdraw(
          this.$store.state.account,
          this.pool.id,
          ethers.utils.parseUnits(this.withdrawAmount, this.pool.decimals)
        );
        await tx.wait();
        this.loadData();
      } catch (error) {
        console.log("onUnstake", error);
      } finally {
        this.unstaking = false;
      }
    },
    async onClaim() {
      this.claiming = true;
      try {
        const tx = await getFairLaunchContract().harvest(this.pool.id);
        await tx.wait();
        this.loadData();
      } catch (error) {
        console.log("onClaim", error);
      } finally {
        this.claiming = false;
      }
    },
    async calcAPY() {
      try {
        const yearlyUSDReward = ethers.BigNumber.from((365 * 24 * 60 * 60) / 3)
          .mul(this.tokenPrice)
          .mul(this.poolInfo.allocPoint)
          .div(this.totalAllocPoint);
        let totalStakeUSDValue, busd, busdInLp;
        let pancakeSwap, alpacaPrice;
        switch (this.pool.id) {
          case 0:
            // AkitaBUSD LP
            busd = new ethers.Contract(busdAddress, ERC20, getProvider());
            busdInLp = (await busd.balanceOf(this.poolInfo.stakeToken)).mul(2);
            totalStakeUSDValue = busdInLp
              .mul(await this.stakeToken.balanceOf(fairLaunchAddress))
              .div(await this.stakeToken.totalSupply());
            break;
          case 1:
            // BUSD
            busd = new ethers.Contract(busdAddress, ERC20, getProvider());
            totalStakeUSDValue = await busd.balanceOf(fairLaunchAddress);
            break;
          case 2:
            // Alpaca
            pancakeSwap = new ethers.Contract(
              pancakeSwapRouter,
              PancakeSwapABI,
              getProvider()
            );
            alpacaPrice = await pancakeSwap.getAmountsOut(
              ethers.utils.parseEther("1"),
              [alpacaAddress, wbtcAddress, busdAddress]
            );
            alpacaPrice = alpacaPrice[alpacaPrice.length - 1];
            totalStakeUSDValue = (
              await this.stakeToken.balanceOf(fairLaunchAddress)
            )
              .mul(alpacaPrice)
              .div(ethers.utils.parseEther("1"));
            break;
        }

        this.apy = yearlyUSDReward
          .mul(100)
          .div(totalStakeUSDValue)
          .toString();
      } catch (error) {
        console.log("calcAPY", error);
      }
    },
    async loadData() {
      this.poolInfo = await getFairLaunchContract().poolInfo(this.pool.id);
      const signer = getProvider().getSigner();
      this.stakeToken = new ethers.Contract(
        this.poolInfo.lpToken,
        ERC20,
        getProvider()
      ).connect(signer);
      this.calcAPY();
      this.needApprove = (
        await this.stakeToken.allowance(
          this.$store.state.account,
          fairLaunchAddress
        )
      ).lt(ethers.utils.parseUnits("10000000000", this.pool.decimals));
      this.balance = ethers.utils.formatEther(
        await this.stakeToken.balanceOf(this.$store.state.account)
      );
      this.staked = ethers.utils.formatEther(
        (
          await getFairLaunchContract().userInfo(
            this.pool.id,
            this.$store.state.account
          )
        ).amount
      );
      this.rewards = ethers.utils.formatEther(
        await getFairLaunchContract().pendingReward(
          this.pool.id,
          this.$store.state.account
        )
      );
    },
  },
};
</script>

<style></style>
